body {
  #single-product-builder {
    padding: 85px 80px 80px;
    background: #FAFBFB;
    .title-section {
      padding: 20px 30px;
      height: 93px;
      background-color: #FFF;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      box-sizing: border-box;
      box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.05);
      .logo {
        position: absolute;
        left: 30px;
        top: 20px;
      }
      h2 {
        text-align: center;
        font-family: Lato;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;
        color: #000000;
        margin: 0px;
      }
    }
    .product-builder-content {
      display: flex;
      flex-wrap: wrap;
      .product-container {
        flex: 1;
        background: #F2F2F2;
        padding: 30px 50px;
        #product-container {
          width: 100%;
          height: 100%;
          .react-transform-wrapper {
            width: 100%;
            height: 100%;
            max-height: 450px;
            .react-transform-component {
              width: 100%;
              height: 100%;
            }
            .wall-wrapper {
              width: 100%;
              height: 100%;
              max-height: 450px;
              background-color: #CCAC7B;
              background-size: 100% 100%;
              padding: 70px 50px 0px 50px;
              box-sizing: border-box;
              &.grid-wall {
                background-image: url(./assets/grid.svg);
              }
              &.single-grid-wall {
                background-image: url(./assets/single_grid.svg);
                background-size: 120%;
              }
              .outline-door {
                box-sizing: border-box;
                border-top: 20px solid #FFF;
                border-left: 17px solid #FFF;
                border-right: 17px solid #FFF;
                height: 100%;
                width: 100%;
                .inline-door {
                  width: 100%;
                  height: 100%;
                  box-sizing: border-box;
                  border-top: 7px solid #A6A7A8;
                  border-left: 9px solid #C9C9C9;
                  border-right: 9px solid #C9C9C9;
                  .inline-wrapper {
                    padding-top: 24px;
                    padding-left: 20px;
                    padding-right: 20px;
                    background-color: #ADA487;
                    width:100%;
                    height: 100%;
                    box-sizing: border-box;
                    background-image: url(./assets/door_background.png);
                    background-position: 0px -10px;
                    .window-wrapper {
                      display: flex;
                      flex-wrap: wrap;
                      justify-content: space-between;
                      > .window-item {
                        flex-basis: calc((100% - 90px) / 4);
                        width: calc((100% - 90px) / 4);
                        margin-bottom: 20px;
                        height: 65px;
                        border: 1.04035px dashed #FFFFFF;
                        box-sizing: border-box;
                        position: relative;
                        .btn {
                          z-index: 2;
                          width: 40px;
                          height: 40px;
                          border-radius: 50%;
                          background: #FFFEFE;
                          box-shadow: 0px 6.16667px 7.70833px 4.625px rgba(106, 100, 100, 0.19);
                          display: flex;
                          justify-content: center;
                          align-items: center;
                          position: absolute;
                          top: calc(50% - 20px);
                          left: calc(50% - 20px);
                          transition: all .25s;
                          cursor: pointer;
                          &:hover {
                            background-color: #FEFEFE;
                          }
                          opacity: 0;
                        }
                        &:not(.disableWindow) {
                          &::before {
                            position: absolute;
                            content: '';
                            width: 100%;
                            height: 100%;
                            background: rgba(255, 255, 255, 0.28);
                            box-sizing: border-box;
                            top: 0px;
                            left: 0px;
                            opacity: 0;
                          }
                          &.no-window {
                            &:hover {
                              &::before {
                                opacity: 1;
                              }
                              .btn.btn-add {
                                opacity: 1;
                              }
                            }
                          }
                          &.active-window {
                            &:hover {
                              .btn.btn-remove {
                                opacity: 1;
                              }
                            }
                            border: 4px solid #FFF;
                            background: #5f5250;
                          }
                        }
                        &.disableWindow {
                          border: none;
                        }
                      }
                    }
                    .vents-wrapper {
                      display: flex;
                      flex-wrap: wrap;
                      justify-content: space-between;
                      > * {
                        flex-basis: calc((100% - 90px) / 4);
                        img {
                          width: 100%;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          .wallSettings {
            margin-top: 50px;
            display: flex;
            align-items: center;
            .wall-item {
              margin-right: 18px;
              // padding: 4px;
              box-sizing: border-box;
              width: 40px;
              height: 40px;
              display: flex;
              justify-content: center;
              align-items: center;
              border-radius: 50%;
              button {
                width: 30px;
                height: 30px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 50%;
                background-color: none;
                box-shadow: none;
                border: none;
                padding: 0px;
                &.grid {
                  background-image: url(./assets/img_grid.png);
                }
                &.single-line {
                  background-image: url(./assets/img_single_grid.png);
                }
                &.single {
                  background-color: #CCAC7B;
                }
              }
            }
          }
          .zoom-control-panel {
            background-color: #FFF;
            border-radius: 16px;
            margin-left: auto;
            margin-bottom: 60px;
            display: flex;
            align-items: center;
            width: fit-content;
            .size-section,
            .zoom-control {
              padding: 15px 20px;
            }
            .zoom-control {
              display: flex;
              align-items: center;
              border-right: 1px solid #F2F2F2;
              .zoom-value {
                user-select: none;
                font-family: Lato;
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 14px;
                color: #000000;
                margin: 0px 10px;
              }
              .btn-zoom {
                cursor: pointer;
                &:hover {
                  svg {
                    path {
                      transition: all .25s;
                      stroke: #1396E7;
                    }
                  }
                }
              }
            }
            .size-section {
              span {
                font-family: Lato;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 17px;
                color: #878787;
                user-select: none;
              }
            }
          }
        }
      }
      .product-custom-bar {
        width: 270px;
        background-color: #FFF;
        .setting-title-section {
          padding: 20px 30px;
          label {
            font-family: Lato;
            font-style: normal;
            font-weight: 600;
            font-size: 12px;
            line-height: 14px;
            text-transform: uppercase;
            color: #878787;
          }
        }
        .product-setting-item-component {
          padding: 15px 30px;
          border-top: 1px solid rgba(236, 236, 236, 0.8);
          display: flex;
          flex-direction: column;
          label {
            font-family: Lato;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 14px;
            color: #333333;
          }
          .size-settings-wrapper {
            display: flex;
            flex-direction: column;
            margin-top: 10px;
            span {
              font-family: Lato;
              font-style: normal;
              font-weight: normal;
              font-size: 10px;
              line-height: 12px;
              text-transform: uppercase;
              color: rgba(135, 135, 135, 0.6);
              margin-right: 5px;
              &.label {
                display: flex;
                align-items: center;
                width: 12px;
              }
            }
            input {
              max-width: 75px;
              border-radius: 4px;
            }
            .input-wrapper {
              display: flex;
              align-items: center;
              input {
                margin: 0px 5px;
              }
              span {
                height: 100%;
                font-size: 26px;
              }
            }
            .height-wrapper {
              margin-top: 15px;
            }
          }
          &.window-settings {
            .description {
              font-family: Lato;
              font-style: normal;
              font-weight: normal;
              font-size: 10px;
              line-height: 14px;
              color: #878787;
              max-width: 165px;
              margin-top: 10px;
            }
          }
          &.lock-placement-settings {
            .button-wrapper {
              margin-top: 15px;
              button {
                margin-right: 10px;
                width: 80px;
                height: 22px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-family: Lato;
                font-style: normal;
                font-weight: normal;
                font-size: 10px;
                line-height: 12px;
                color: rgba(51, 51, 51, 0.5);
                border-radius: 56px;
                border: none !important;
                box-shadow: none !important;
                background: #F2F2F2 !important;
                &.active {
                  background: #1396E7 !important;
                  color: #FFF;
                }
              }
            }
          }
          &.colors-settings {
            .colors-wrapper {
              margin-top: 10px;
              .color-item {
                margin-right: 8px;
                padding: 0px;
                width: 24px;
                height: 24px;
                display: flex;
                border-radius: 50%;
                &:not(:last-child) {
                  margin-right: 10px;
                }
                display: flex;
                justify-content: center;
                align-items: center;
                button {
                  width: 18px;
                  height: 18px;
                  border-radius: 50%;
                  box-sizing: border-box;
                  padding: 0px;
                  border: none !important;
                  box-shadow: none !important;
                }
              }
            }
            .addPrice {
              padding: 2px 8px;
              background-color: #EBFFF0;
              font-family: Lato;
              font-style: normal;
              font-weight: normal;
              font-size: 10px;
              line-height: 12px;
              color: #028C29;
              border-radius: 4px;
            }
          }
          &.addCartButton {
            padding-bottom: 50px;
            button {
              padding: 15px;
              background-color: #1396E7;
              font-family: Lato;
              font-style: normal;
              font-weight: 500;
              font-size: 12px;
              line-height: 14px;
              color: #FFFFFF;
              border: 1px solid #1396E7;
              transition: all .25s;
              &:hover {
                opacity: 0.6;
              }
            }
          }
          &.price-section {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            label {
              font-family: Lato;
              font-style: normal;
              font-weight: normal;
              font-size: 12px;
              line-height: 14px;
              color: #878787;
            }
            p {
              margin: 0px;
              font-family: Lato;
              font-style: normal;
              font-weight: 600;
              font-size: 12px;
              line-height: 14px;
              color: #333333;
            }
          }
        }
      }
    }
  }
}

.d-flex {
  display: flex;
  &.align-items-center {
    align-items: center;
  }
  &.justify-content-between {
    justify-content: space-between;
  }
}

/* The container */
.radio-container {
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }
  .checkmark {
    position: absolute;
    top: -2px;
    left: 0;
    height: 16px;
    width: 16px;
    background-color: #FFF;
    border: 1px solid #1396e7;
    border-radius: 5px;
  }
  &:hover {
    input ~ .checkmark {
      background-color: #ccc;
    }
  }
  input:checked ~ .checkmark {
    background-color: #FFF;
    &::after {
      display: block;
    }
  }
  .checkmark::after {
    content: "";
    position: absolute;
    display: none;
    top: 2px;
    left: 2px;
    width: 12px;
    height: 12px;
    border-radius: 3px;
    background: #1396e7;
  }
}

.slider-bar {
  padding: 20px 0px;
  flex: 1;
  .horizontal-slider {
    flex: 1;
    margin-right: 24px;
    margin-top: 20px;
    height: 4px;
    border-radius: 13px;
    .example-thumb {
        width: 10px;
        height: 10px;
        top: -3px;
        border-radius: 50%;
        background-color: #1396E7;
        font-size: 0px;
        outline: none !important;
        cursor: pointer;
    }
    .example-track.example-track-0 {
      background-color: #1396E7;
      height: 4px;
      overflow: hidden;
    }
    .example-track.example-track-1 {
      background-color: #F1F1F1;
      height: 4px;
      overflow: hidden;
    }
  }
  .minmax {
      padding-left: 20px;
      padding-right: 65px;
  }
}